.ModalOverlay {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 25px;
  overflow: hidden; }

.CancelButton {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 50px;
  height: 50px;
  color: white;
  font-size: 18px; }
  .CancelButton:hover {
    background-color: #666; }

.trans-start .ModalContent {
  opacity: 0;
  top: -20px; }

.ModalContent {
  pointer-events: all;
  position: absolute;
  right: 0;
  padding: 0 15px 15px 15px;
  background-color: rgba(0, 0, 0, 0.85);
  color: white;
  box-sizing: border-box;
  max-height: 100%; }
  .trans-end .ModalContent {
    transition: opacity 0.5s, top 0.5s, bottom 0.5s, left 0.2s, right 0.2s;
    opacity: 1;
    top: 0; }
    .trans-end .ModalContent.HiddenPopup {
      top: -20px;
      opacity: 0; }
  .trans-end .narrow .ModalContent {
    top: 25px; }
  .ModalContent .Title {
    display: flex;
    align-items: center;
    text-align: center;
    padding: 0 15px 0 50px;
    margin: 0 -15px 15px -15px;
    border-bottom: solid 1px #444;
    min-height: 50px;
    position: relative; }
    .ModalContent .Title button {
      font-size: 25px;
      min-width: 35px;
      margin: 0 10px; }
      .ModalContent .Title button.active {
        border-bottom: solid 2px white; }
    .ModalContent .Title .FlexTitle {
      display: inline-block;
      min-width: calc(100% - 115px); }
  .ModalContent .PopupBody {
    overflow: auto; }
  .ModalContent .OptionRow {
    padding: 5px 0;
    margin: 5px 0 10px; }
    .ModalContent .OptionRow label {
      display: block;
      width: 100%; }
      .ModalContent .OptionRow label > span {
        display: inline-block;
        margin: 0 12px;
        vertical-align: top;
        min-width: 100px;
        width: 50%;
        text-align: right; }
        [dir="rtl"] .ModalContent .OptionRow label > span {
          text-align: left; }
    .ModalContent .OptionRow input[type="radio"] {
      vertical-align: text-top; }
  .ModalContent .FieldRow {
    margin: 10px 0; }
    .ModalContent .FieldRow div {
      display: inline-block; }
  .ModalContent .FieldLabel {
    width: 38%;
    text-align: left;
    padding: 0 10px;
    box-sizing: border-box; }
  .ModalContent .FieldValue {
    width: 23%;
    text-align: right; }
    .ModalContent .FieldValue input {
      width: 100%;
      color: white;
      border: none;
      font-size: 25px;
      background-color: #ffffff30;
      vertical-align: middle; }
  .ModalContent .FieldAction {
    width: 23%;
    padding: 0 10px;
    box-sizing: border-box; }
  .ModalContent .ActionBar {
    margin: 20px 0 0 0;
    text-align: center; }
  .ModalContent .FieldAction button {
    background-color: #666;
    border: none;
    color: white;
    padding: 5px 15px;
    font-size: 17px;
    vertical-align: middle; }
    .ModalContent .FieldAction button:hover {
      background-color: black; }
  .ModalContent .CancelButton {
    font-size: 24px; }
  .ModalContent .CommandsList {
    text-align: center;
    direction: ltr; }
    .ModalContent .CommandsList button {
      width: 80px;
      height: 80px;
      font-size: 25px; }
      .ModalContent .CommandsList button .CommandLabel {
        font-size: 12px; }
  .ModalContent #SearchForm > * {
    display: inline-block;
    height: 40px;
    vertical-align: top; }
  .ModalContent #SearchForm .SearchInput {
    background-color: transparent;
    color: white;
    border: none;
    font-size: 25px;
    border: solid 1px silver;
    box-sizing: border-box;
    width: calc(100% - 80px); }
  .ModalContent #SearchForm button[type="submit"] {
    width: 60px;
    font-size: 25px;
    border: solid 1px silver;
    margin: 0; }
  .ModalContent #SearchHistory {
    padding: 5px 0;
    white-space: nowrap;
    overflow-y: hidden;
    overflow-x: auto;
    height: 44px; }
    .ModalContent #SearchHistory button {
      font-size: 15px;
      padding: 3px 5px;
      border: solid 1px silver;
      margin: 0 3px; }
  .ModalContent .link {
    cursor: pointer;
    padding: 5px; }
    .ModalContent .link:hover {
      color: #ffffaa;
      background-color: #666; }
    .ModalContent .link * {
      pointer-events: none; }
  .ModalContent .ResultInfo {
    color: #aaffff;
    background-color: #444;
    display: inline-block;
    min-width: 100px;
    text-align: left;
    padding: 0 5px; }
  .ModalContent .ResultsInfo {
    color: yellow;
    height: 35px;
    line-height: 30px; }
  .ModalContent .ResultsList {
    margin: 0;
    padding-inline-start: 30px; }
    .ModalContent .ResultsList .MoreResults {
      width: 100%;
      background-color: #999;
      padding: 10px;
      color: black;
      cursor: pointer; }
      .ModalContent .ResultsList .MoreResults:hover {
        background-color: #ddd; }
    .ModalContent .ResultsList .ResultItem button {
      text-align: right;
      font-size: 16px;
      line-height: 25px; }
      .ModalContent .ResultsList .ResultItem button span {
        pointer-events: none; }
      .ModalContent .ResultsList .ResultItem button .hiword {
        color: yellow; }
  .ModalContent a {
    color: white;
    text-decoration: none; }
  .ModalContent .TafseerVerse {
    color: yellow; }

#SearchPopup .Title {
  flex-basis: 180px; }
  #SearchPopup .Title > div {
    width: 100%;
    text-align: right; }

#IndexPopup .Title {
  align-items: stretch; }
  #IndexPopup .Title button {
    height: 100%;
    min-width: 60px;
    margin: 0; }
